import { substring } from 'utils/index'
import { contMethods } from 'utils/contMethods'
import { mapGetters } from 'vuex'
export default {
  name: 'manage',
  data () {
    return {
      havePage: false,
      balan:[]
    }
  },
  components: {
   
  },
  created: function () {
      var that = this
      that.dataInit()
  },
  computed: {
    ...mapGetters([
      'info',
      'abi',
      'contractAddr'
    ])
  },
  methods: {
      dataInit () {
          var that = this 
          that.$store.dispatch('UserInit', '');
          if (that.info.length === 0) {
              that.havePage = false;
          } else {
              that.havePage = true; 
              Promise.all(that.info.map(function(elem){             
                  return new Promise(function(resolve, reject){
                      contMethods.constantMethod('balanceOf', that.abi, that.contractAddr, [elem.addresses], function(value){       
                           resolve(value);
                      })
                  })
              })).then(function(data){
                  that.balan = data
              })
          }           
      },
      manageLink (value) {
          var that = this
          let len = value
          that.$store.dispatch('currentUpdate', {current: len}).then(() => { 
              this.$router.push({
                  path: 'detail', 
                  name: 'detail'
              })
          }).catch(err => {
              that.$vux.toast.text('钱包切换失败请刷新后重试！', 'top')
          });
      },
      addressInit (address) {
          var that = this
          return substring(address)
      }
  }
} 



